 margin-bottom: 10px; */
}

h4 small, .h4 small, h5 small, .h5 small, h6 small, .h6 small, h4 .small, .h4 .small, h5 .small, .h5 .small, h6 .small, .h6 .small {
 font-size: 75%;
}

h1, .h1 {
 font-weight:600;
 font-size: 28px;
}

h2, .h2 {
 /* font-weight:600; */
 font-family:'SofiaProSoftBold';
 font-weight:normal;
 font-size: $fontsize4;
 @mixin atTnsm {
  }
 @mixin atSmmd {
  font-size: $fontsize5;
  }
 @mixin atMd {
  font-size: $fontsize6;
  }
 @mixin atLg {
  }
}

h3, .h3 {
 font-family:'SofiaProSoftMedium';
 font-weight:normal!important;
 font-size: $fontsize3;
 @mixin atTnsm {
 }
 @mixin atSmmd {
  font-size: $fontsize4;
 }
 @mixin atMd {
  font-size: $fontsize5;
 }
 @mixin atLg {
 }
}

h4, .h4 {
 font-weight:700;
 font-size: $fontsize0;
 margin-bottom: 0;
 margin-top: 0;
line-height: 1.25;

}

h5, .h5 {
 font-weight:700;
 font-size: 0.875rem;
}

h6, .h6 {
 font-weight:700;
 font-size: 12px;
}

.post-body,
.aws-author--section,
.aws-single-column,
.aws-section--dual
 {
 p {
  line-height: $bodycopylineheight;
  margin-bottom: 1rem;
  margin: 0 0 .875rem;
  color:$slate;
  font-weight: normal;
  font-family: 'SofiaProSoftRegular';
  strong {
   font-weight: normal;
   font-family: 'SofiaProSoftBold';
   color:black;
  }
  a:only-child {
   letter-spacing:0.05em;
   font-family:$fontfam4;
   font-weight:normal;
   &:after {
    position:relative;
    display:inline-block;
    font-family:'aws_icons';
    content:'\e818';
    font-size:133%;
    margin-left: 0.25ex;
    bottom:-0.2ex;
    margin-top:-0.5ex;
   }
  }
 }
 h2 {
  color:$charcoal;
 }
 h3 {
  font-weight:700;
  font-size: $fontsize3;
  @mixin atMd { font-size: $fontsize3; }
  &, strong {
  font-weight:700;
  margin-bottom: 0.625rem;
  margin-top: 1.375rem;
  letter-spacing: -0.015em;
  word-spacing:-0.015em;
  color:$charcoal;
 }
 }
 ul {
  li {
   margin-bottom: 0.5625rem;
  }
 }
 *:last-child {
  margin-bottom: 0;
 }
}
a {
 transition: color $transtime0 ease-in-out;
 &:hover, &:active {
  color:black;
 }
}

p {
 margin-block-start:0;
 margin-block-end:0;
}
